.sets__container {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  background-color: #2c2c2c;
  height: 150px;
}

.sets__count {
  font-size: 2.5em;
  font-weight: bold;
  padding: 0;
}

.sets__text {
  font-size: 0.5em;
  padding: 5px;
}

.sets_buttons__container {
  display: flex;
  flex-direction: row;
  justify-content: space-around;
  align-items: center;
  background-color: #2c2c2c;
  border-radius: 5px;
  width: 75%;
  border: 1px solid white;
}

.sets_buttons__container button {
  width: 50%;
  height: 40px;
  line-height: 29px;
  font-weight: bold;
  font-size: 1.5em;
}

.sets_buttons__container button:disabled {
  color: grey;
}

.sets_buttons__container button:first-of-type {
  border-top-left-radius: 5px;
  border-bottom-left-radius: 5px;
}

.sets_buttons__divisor {
  height: 10px;
  border-left: 1px solid #59636f;
}
